import LibGenerateTestUserSig from "./lib-generate-test-usersig-es.min";
/**
 *  SDKAppId SDKAppId
 *
 * [](https://console.cloud.tencent.com/rav )  SDKAppId
 * 
 */

const SDKAPPID = 1400402305;
/**
 * 
 * <p>
 * 
 * 7 x 24 x 60 x 60 = 604800 = 7 
 */

const EXPIRETIME = 604800;
/**
 * 
 *
 * step1. [](https://console.cloud.tencent.com/rav )
 * step2. 
 * step3.  UserSig 
 *
 * Demo UserSig 
 * https://cloud.tencent.com/document/product/647/17275#Server
 */

const SECRETKEY = 'ddfff85f702859956635feb79dbbd0dd3eff12af4e5076e6b7e554056946c376';
/*
 * Module:   GenerateTestUserSig
 *
 * Function:  UserSigUserSig 
 *            SDKAppIDUserID  EXPIRETIME  HMAC-SHA256
 *
 * Attention:  App 
 *
 *             UserSig SDK 
 *             SECRETKEY  Web 
 *             UserSig 
 *
 *             UserSig  App  UserSig
 *             App
 *
 * Referencehttps://cloud.tencent.com/document/product/647/17275#Server
 */

function genTestUserSig(userID) {
  const generator = new LibGenerateTestUserSig(SDKAPPID, SECRETKEY, EXPIRETIME);
  const userSig = generator.genTestUserSig(userID);
  return {
    sdkAppID: SDKAPPID,
    userSig: userSig
  };
}

function setData(obj,callback){  
	      let that = this;  
	      let keys = [];  
	      let val,data;  
	      Object.keys(obj).forEach(function(key){  
	           keys = key.split('.');  
	           val = obj[key];  
	           data = that.$data;  
	           keys.forEach(function(key2,index){  
	               if(index+1 == keys.length){  
	                   that.$set(data,key2,val);  
	               }else{  
	                   if(!data[key2]){  
	                      that.$set(data,key2,{});  
	                   }  
	               }  
	               data = data[key2];  
	           })  
	      });
		  setTimeout(()=>{
			  callback&&callback()
		  },500)
		  
	  }

module.exports = {
  genTestUserSig,
  setData
};