// stream  player
import { DEFAULT_PLAYER_CONFIG } from "../common/constants";

class Stream {
  constructor(options) {
    Object.assign(this, DEFAULT_PLAYER_CONFIG, {
      userID: '',
      // stream userID
      streamType: '',
      // stream  [main small] aux
      isVisible: true,
      // Q puserplayer iOS player
      hasVideo: false,
      hasAudio: false,
      playerContext: undefined // playerContext component context

    }, options);
  }
  /**
   * 
   */


  switchMainSmallStream() {
    if (this.streamType === 'main' || this.streamType === 'small') {// 
      //  streamType  streamURL  componentContext setData()
    } else {
      console.log('aux ');
    }
  }

  reset() {
    if (!this.playerContext) {
      this.playerContext.stop();
      this.playerContext = null;
    }

    Object.assign(this, DEFAULT_PLAYER_CONFIG, {
      userID: '',
      // stream userID
      streamType: '',
      // stream  [main small] aux
      streamID: '',
      isVisible: true,
      hasVideo: false,
      hasAudio: false,
      playerContext: undefined
    });
  }

}

export default Stream;